.projects {
  position: relative;
  z-index: 100;
  text-align: center;
  background-color: var(--color-black);
}

.divinest-light__intro {
  position: relative;
  width: 100%;
}

.divinest-light__bg {
  display: flex;
  width: 100%;
  height: 100%;
  background-image: url(./../img/divinest-light.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-black);
}

.divinest-light__video {
  width: 100%;
  height: 100%;
}

.divinest-light__video--mob {
  display: none;
  opacity: 0;
}

.divinest-light__link {
  position: absolute;
  left: 50%;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
  border-radius: 20px;
  width: 480px;
  height: 90px;
  font-family: "Roboto Mono";
  font-weight: 400;
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  color: var(--color-white);
  background-color: var(--color-purple);
  transform: translate(-50%, -50%);
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.divinest-light__link:hover {
  color: var(--color-purple);
  background-color: var(--color-white);
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.divinest-light__dsr {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 70px;
}

.divinest-light__title {
  margin-bottom: 35px;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 32px;
  text-transform: uppercase;
  color: var(--color-white);
}

.divinest-light__text {
  max-width: 605px;
  line-height: 30px;
  color: var(--color-white);
}

.divinest-light__text:not(:last-child) {
  margin-bottom: 27px;
}

.projects-body {
  position: relative;
  padding-bottom: 100px;
  background-color: var(--color-dark);
}

.mobile-projects {
  display: flex;
  justify-content: space-between;
  margin-bottom: 140px;
  padding-top: 85px;
}

.game {
  width: 25%;
}

.game__link {
  display: block;
  width: 100%;
}

.game__link:hover .game__title {
  color: var(--color-purple);
  transition: color 0.3s ease-in-out;
}

.game__bg {
  display: flex;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: 98%;
  background-repeat: no-repeat;
}

.game__video {
  width: 100%;
  height: 100%;
}

.game__text {
  padding-top: 30px;
  text-align: left;
}

.game__title {
  margin-bottom: 20px;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 32px;
  text-transform: uppercase;
  color: var(--color-white);
  transition: color 0.3s ease-in-out;
}

.game__dsr {
  font-family: "Roboto Mono";
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: var(--color-white);
}

.game:not(:last-child) {
